import {Injectable} from "@angular/core";

//f2f718cb46cb777
//5af377ae445d2a1
@Injectable()
export class ConnectionService {
    private url = 'https://api.imgur.com/'
    public token;
    private clientId = '5af377ae445d2a1'

    connection() {
        let oauth = open(this.url +'oauth2/authorize?client_id=' + this.clientId + '&response_type=token', '_self', 'location=no');
        oauth.addEventListener("loadstart", (event:any) => {
            if ((event.url).indexOf('access_token') !== -1) {
                this.token = event.url.slice(event.url.indexOf('access_token=') + 'access_token='.length);
                oauth.close();
                return (this.token);
            } else {
                console.log("Error loging in");
            }
        })
    }
}




